"use strict";

var React = React;
var BrowserFolderContentItem = BrowserFolderContentItem;
var ctx = ctx;
var folderStore = folderStore;
var contentsStore = contentsStore;
var contentsActions = contentsActions;

var BrowserFolderContents = React.createClass({
	displayName: 'BrowserFolderContents',
	_getState: function _getState() {
		return {
			contents: contentsStore.get(),
			selected: contentsStore.getSelected()
		};
	},
	getInitialState: function getInitialState() {
		return this._getState();
	},
	componentDidMount: function componentDidMount() {
		contentsStore.addListener(this.contentsUpdated);
	},
	componentWillUnmount: function componentWillUnmount() {
		contentsStore.removeListener(this.contentsUpdated);
	},
	contentsUpdated: function contentsUpdated() {
		this.setState(this._getState());
	},
	onDrag: function onDrag() {
		this.refs.wrapper.style['backgroundColor'] = '#efefef';
	},
	onDragFinished: function onDragFinished() {
		this.refs.wrapper.style['backgroundColor'] = 'transparent';
	},
	render: function render() {
		var _this = this;

		var contents = this.state.contents.map(function (c) {
			return React.createElement(BrowserFolderContentItem, { key: c.id,
				content: c,
				selected: _this.state.selected });
		});

		return React.createElement(
			'div',
			{ className: 'browser-contents',
				ref: 'wrapper',
				onDragEnter: this.onDrag,
				onDragLeave: this.onDragFinished,
				onDragEnd: this.onDragFinished,
				onDrop: this.onDragFinished },
			contents
		);
	}
});